                  @click="changeLock"
                />
              </template>
            </el-input>
          </el-form-item>
          <!-- <el-form-item style="position: relative" prop="captcha">
            <el-input
              v-model="loginForm.captcha"
              name="logVerify"
              placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å"
              style="width: 60%"
            />
            <div class="vPic">
              <img
                v-if="picPath"
                :src="picPath"
                alt="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å"
                @click="loginVerify()"
              >
            </div>
          </el-form-item> -->
          <el-form-item>
            <el-button type="primary" style="width: 46%" @click="toRegister"
              >ÂâçÂæÄÊ≥®ÂÜå</el-button
            >
            <el-button
              type="primary"
              style="width: 46%; margin-left: 8%"
              @click="submitForm"
              >Áôª ÂΩï</el-button
            >
          </el-form-item>
        </el-form>
      </div>
      <div class="login_panle_right" />
