<script>
import { mapActions } from "vuex";
import { useRouter } from "vue-router";

export default {
  name: "Login",
  setup() {
    const router = useRouter();
    function toRegister() {
      router.push("/register");
    }
    function toHome() {
      router.push("/home");
    }
    return {
      toRegister,
      toHome,
    };
  },
  data() {
    const checkUsername = (rule, value, callback) => {
      if (value.length < 5) {
        return callback(new Error("ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÁî®Êà∑Âêç"));
      } else {
        callback();
      }
    };
    const checkPassword = (rule, value, callback) => {
      if (value.length < 6) {
        return callback(new Error("ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÂØÜÁ†Å"));
      } else {
        callback();
      }
    };
    return {
      curYear: 0,
      lock: "lock",
      loginForm: {
        username: "123456",
        password: "200000",
      },
      rules: {
        username: [{ validator: checkUsername, trigger: "blur" }],
        password: [{ validator: checkPassword, trigger: "blur" }],
        // captcha: [
        //   { required: true, message: 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å', trigger: 'blur' },
        //   {
        //     message: 'È™åËØÅÁ†ÅÊ†ºÂºè‰∏çÊ≠£Á°Æ',
        //     trigger: 'blur',
        //   },
        // ],
      },
      logVerify: "",
      picPath: "",
    };
  },

  created() {
    this.curYear = new Date().getFullYear();
  },

  methods: {
    ...mapActions("user", ["LoginIn"]),
    async login() {
      return await this.LoginIn(this.loginForm);
    },
    async submitForm() {
      this.$refs.loginForm.validate(async (v) => {
        if (v) {
          await this.login();
          this.toHome();
        } else {
          this.$message({
            type: "error",
            message: "ËØ∑Ê≠£Á°ÆÂ°´ÂÜôÁôªÂΩï‰ø°ÊÅØ",
            showClose: true,
          });
          return false;
        }
      });
    },
    changeLock() {
      this.lock = this.lock === "lock" ? "unlock" : "lock";
    },
  },
};
</script>

